import React, { useState } from 'react';
import tonConnectInstance from './tonConnectInstance';
import './App.css';

const App = () => {
  const [connected, setConnected] = useState(false);

  const handleConnect = async () => {
    try {
      const wallet = await tonConnectInstance.connect({
        bridgeUrl: 'https://bridge.tonapi.io/bridge',
      });
      setConnected(true);
      alert('Connected to TON Wallet');
    } catch (error) {
      console.error('Failed to connect to TON Wallet:', error);
      setConnected(false);
    }
  };

  return (
    <div className="App">
      <header className="App-header">
        <h1>Pig Mini App</h1>
        <nav>
          <ul>
            <li>Usernames</li>
            <li>Numbers</li>
            <li>Stars</li>
            <li>Premium</li>
          </ul>
        </nav>
      </header>
      <main>
        <h2>Buy and Sell Usernames</h2>
        <p>Secure your name with blockchain in an ecosystem of 950+ million users...</p>
        {connected ? (
          <p>Wallet Connected</p>
        ) : (
          <button onClick={handleConnect}>Connect to TON Wallet</button>
        )}
      </main>
    </div>
  );
};

export default App;
